import styled from "styled-components";
import PortfolioBgImg from "../../assets/bg/portfolio.jpg";
import * as React from "react";
import ScrollImg from "../../assets/bg/scroll_icon.png"
import {useCallback} from "react";
import {scroller} from "react-scroll/modules";

export function ScrollWidget() {
    const goToFn = useCallback((ev: string) => {
        scroller.scrollTo(ev, {
            duration: 500,
            delay: 100,
            smooth: 'easeInOutQuint',
        })
    }, [])
    return <ScrollStyle>
        <h3 className="right_bottom">

            <div className="scroll"  onClick={() => goToFn('Portfolio_01')}>
                <span>SCROLL</span>
                <img src={ScrollImg} alt=""/>
            </div>
        </h3>
    </ScrollStyle>
}


const ScrollStyle = styled.div`
  height: 100vh;
  background-image: url("${PortfolioBgImg}");
  background-size: cover;
  position: relative;
  background-position: center;

  .right_bottom {

    max-width: 1400px;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 80px;

    p {
      padding: 8px 5%;
      text-align: left;
      font-family: 'Arial';
      font-style: normal;
      font-weight: 400;
      font-size: 26px;
      line-height: 41px;
      display: flex;
      align-items: center;
      color: #FFFFFF;

      text-shadow: 0px 0px 8px #000000;
    }



    .scroll {
      padding-top: 100px;
      text-align: center;
      cursor: pointer;

      span {
        font-family: 'Arial';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        text-shadow: 0px 0px 8px #000000;
        color: #FFFFFF;
      }

      img {
        display: block;
        width: 30px;
        height: 10px;
        margin: 0 auto;
      }
    }
  }

`