type CellItemType = {
    img: string,
    subName: string,
    inner: string[]
}

export function CellItemWidget(props: CellItemType) {
    return (
        <CellItemStyle>
            <img src={props.img} alt="" className="left"/>
            <div className="right">
                <h3 className={'sub_name'}>{props.subName}</h3>
                <div className="inner">
                    {
                        props.inner.map((ev) => <h3 key={ev}>{ev}</h3>)
                    }
                </div>
            </div>
        </CellItemStyle>
    );
}

